ul, li {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
}

.app {
  display: flex;
  flex-direction: column;
  margin: 40% auto 0;
  padding: 0 24px;
  font-size: 1.25rem;
}

.formContent {
  display: flex;
  flex-direction: column;
}

.formContent+.formContent {
  margin-top: 20px;
}

.calcBtn {
  margin-top: 12px;
  padding: 4px 0px;
  font-size: 1.15rem;
}

.input {
  padding: 8px 12px;
  font-size: 1.15rem;
}

.display {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  margin: 16px auto 0;
}

.display .number {
  text-align: center;
}